<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure class="section-masthead__media">
				<lazy-image :src="imgSrc" :alt="imgAlt"/>
			</figure>
			<section class="section-masthead__text">
				<h1>Bayer - Aspirin C</h1>
				<p class="subtitle">Revitalizing the brand by finding a new space in the minds of consumers</p>
			</section>
		</header>
		<article class="work">
			<section class="work__detail">
				<h3 class="work__subtitle">Context</h3>
				<p>
					Aspirin+C is a consolidated
					Brand in C&F market
					in Italy but facing
					strong competition
					mainly by combination
					Products, suffering from reduced investments over
					time and absence of
					recent innovation on
					the brand.
				</p>
				<p>
					The brand required
					a clear and differentiated
					positioning in the C&F
					category to compete
					with combination products
					and regain relevance among
					consumers
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Insight</h3>
				<p>
					After conducting in-­depth consumer analysis and looking at consumer context, category landscape and
					brand equity, we challenged previous strategies and decided to change the context and move the brand
					from a positioning of mild on severe symptoms to strong at the very onset of C&F.
				</p>
				<p>
					The new creative platform was built on a new insight, validated with strong results: <em>When I have
					cold or flu I want to get through it as soon as possible but without taking too many medicines</em>.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Creative idea</h3>
				<p>
					We launched a new call to action to all C&F sufferers: PensiamoCi Subito (Let’s think about it right
					away).
				</p>
				<p>
					A highly recognizable claim due to the use of the C icon and a new peer-to-peer body language from
					the brand.
				</p>
				<p>
					The campaign was launched with a new TVC and follow by a contextual digital display campaign the
					extended the “let’s think it right away” concept to relevant themes for users such as weather
					forecast and shopping.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Impact</h3>
				<p>
					Despite the brand being relaunched with fewer GRPs and SOV than 2016, the campaign has:
				</p>
				<ul>
					<li>Helped +C regain market share in a very competitive scenario: +3 points compared to same period
						2016.
					</li>
					<li>Improved advertising awareness and brand linkage: 63% relevant recall vs. 47% of previous
						campaign and vs. 49% of main spender in the market Vivin C
					</li>
				</ul>
			</section>
		</article>
		<video-wrapper src="//www.youtube.com/embed/LADLAhA7hkM"
		               title="Bayer - Aspirin C"/>
		<app-footer/>
	</section>
</template>

<script>

	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import VideoWrapper from './VideoWrapper';

	export default {
		name: 'Philadelphia',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
			VideoWrapper,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead__media {
		height: 100%;
		position: relative;
	}
	
	.section-masthead__media::after {
		content: '';
		position: absolute;
		top: -7px;
		left: 7px;
		width: 100%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		font-size: 5vw;
	}
	
	p {
		margin-bottom: 0;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	.work__detail {
		border: 1px solid var(--color__dark-alpha);
		padding: 0.75em 1.25em;
	}
	
	.work__subtitle {
		background-color: var(--color__light);
		padding: 0 0.5em;
		transform: translateY(-1.5em);
		margin: 0;
		display: inline-block;
	}
	
	.work__subtitle + p {
		transform: translateY(-1.5em);
	}
	
	ul {
		margin: 0;
		list-style: none;
		padding-left: 0;
	}
	
	li {
		position: relative;
		padding-left: 1em;
	}
	
	li:not(:first-child) {
		margin-top: 0.75em;
	}
	
	li::before {
		content: '-';
		transform: scaleX(3) rotate(20deg);
		position: absolute;
		left: 0;
		top: 0;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
		
		.work__detail:not(:first-child) {
			margin-top: 2em;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
		
		article {
			display: flex;
			background: linear-gradient(135deg, rgba(230, 230, 230, 0.2) 50%, var(--color__light-alpha-norm) 50%) no-repeat 0 -65%;
			background-size: 110% 110%;
			padding: 2em 0 0.5em 0.5em;
		}
		
		.work__detail {
			flex: 1;
		}
		
		.work__detail:not(:first-child) {
			margin-left: 1em;
		}
	}
</style>



